.searchForm {
    display: flex;
    flex-direction: column;
    padding: 1rem ;
    align-items: center;
    font-size: 14px;
    flex:1;
    background-color: #191919;
    color: #bababa;
    
    
}

.searchForm input {
    background: #fff;
    font: 10px Arial, Helvetica, sans-serif;
    color: #191919;
    padding: 3px 10px;
    height: 22px;
    width: 100%;
    margin-bottom: 5px;
}

.searchForm label{
    align-self:flex-start;
    font: 14px Arial, Helvetica, sans-serif;
}

.colSection{
    width:100%;
    display: flex;
    justify-content: space-between;
    gap: 1rem 
}

.col{
    display: flex;
    flex-direction: column;
    flex:1;
    align-items: center;
}


.searchForm select {
    background: #fff;
    font: 12px Arial, Helvetica, sans-serif;
    color: #191919;
    float: left;
    padding: 3px 10px;
    height: 22px;
    width: 100%;
    margin-bottom: 5px;
}


.info {
    text-align: left;
    padding-top: 0.5rem;
    font: 13px Arial, Helvetica, sans-serif;
}

.links {
    color: rgb(74, 194, 74);
}

.links:hover {
    color: #8c8c8c
}


@media(max-width:630px) {
    .searchForm{
        padding:1.5rem  4rem
    }
}



@media(max-width:410px) {
    .searchForm{
        padding:1.5rem  2rem
    }
}